/*  */
<style>
  .el-dialog {
    padding-top: 10px;
  }

  .edit-tip {
    margin: -25px auto 24px;
    font-size: 14px;
    color: #8590A6;
    max-width: 240px;
    text-align: center;
  }

  .edit-footer {
    padding-bottom: 10px;
  }
</style>
<template>
  <el-dialog title="" :center=true :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
    <!--<span class="edit-tip"></span>-->
    <el-form label-width="80px" status-icon>
      <el-form-item :label="'' + inputType">
        <el-input v-model.trim="oldInfo" :placeholder="'' + inputType"></el-input>
      </el-form-item>
      <el-form-item :label="'' + inputType">
        <el-input v-model.trim="newInfo" :placeholder="'' + inputType"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer edit-footer">
            <el-button @click="onCancel"> </el-button>
            <el-button type="primary" @click="onConfirm"> </el-button>
        </span>
  </el-dialog>
</template>
<script>
import STORAGE from '../../assets/javascripts/storage.js'
  export default {
    props: ['isVisible', "inputType"],
    data() {
      return {
        dialogVisible: this.isVisible,
        oldInfo: "",
        newInfo: "",
        userinfo: {}
      }
    },
    mounted() {
      let userinfo = localStorage.getItem("USERINFO");
      if (userinfo) {
        userinfo = JSON.parse(userinfo)
        this.userinfo = userinfo[0]
      }
    },
    methods: {
      handleClose(done) {
        this.$confirm('')
            .then(_ => {
              done();
            })
            .catch(_ => {
            });
      },
      onConfirm() {
        switch (this.inputType) {
          case '':
            this.updatePwd()
            break;
          case '':
            this.updatePhone()
            break;
          default:
            this.updateEmail();
        }
      },
      onCancel() {
        this.oldInfo = this.newInfo = "";
        this.dialogVisible = false
      },
      updatePwd() {
        this.$axios.post("/api/users/update_pwd", {
          id: this.userinfo.id,
          oldpwd: this.oldInfo,
          newpwd: this.newInfo
        }).then(res => {
          if (res.data.code === "200") {
            this.$message({
              type: "success",
              message: res.data.message
            });
            this.onCancel()
          }
        })
      },
      updatePhone() {
        this.$axios.post("/api/users/update_phone", {
          id: this.userinfo.id,
          oldphone: this.oldInfo,
          newphone: this.newInfo
        }).then(res => {
          if (res.data.code === "200") {
            this.$message({
              type: "success",
              message: res.data.message
            });
            console.log(this.newInfo)
            let userInfo = STORAGE.GET('USERINFO')
            userInfo[0].phone = this.newInfo 
            STORAGE.SET(userInfo)
            this.onCancel()
          }
        })
      },
      updateEmail() {
        this.$axios.post("/api/users/update_email", {
          id: this.userinfo.id,
          oldemail: this.oldInfo,
          newemail: this.newInfo
        }).then(res => {
          if (res.data.code === "200") {
            this.$message({
              type: "success",
              message: res.data.message
            });

            let userInfo = STORAGE.GET('USERINFO')
            userInfo[0].email = this.newInfo 
            STORAGE.SET(userInfo)
            this.onCancel()
            
          }
        })
      }
    },
    watch: {
      isVisible(val) {
        this.dialogVisible = val
      },
      dialogVisible(val) {
        this.$emit("on-visible-change", val)
      }
    }
  }
</script>



